	module.exports = {"data":{"post":{"html":"<p>Thanks to some powerful antibiotics, it looks like our trip to Berlin can go on as planned. If you hear about any damage in Berlin this weekend, remember, it might not be anti-war protesters. It might be Christopher.</p>\n<p>I'm in bed, but feeling better. The wi-fi reception is good and I can read about the news in Iraq without having to endure the droning TV coverage. The prescription was my first experience with 'aut idem'. My doctor prescribed Ciprobeta, but the pharmacist didn't have that brand in stock and asked if she could substitute Ciprohexal.</p>","fields":{"title":"","date":"21 Mar 2003","url":"https://papascott.de/2003-03-21-cipro-ueber-alles/"}}},"pathContext":{"slug":"/2003-03-21-cipro-ueber-alles/","prev":{"fields":{"title":"Nobody Should Care","date":"20 Mar 2003","slug":"/2003-03-20-nobody-should-care/"}},"next":{"fields":{"title":"27 Hours Berlin","date":"24 Mar 2003","slug":"/2003-03-24-27-hours-berlin/"}}}}

